@import url('https://fonts.googleapis.com/css?family=Exo+2');

html , body {
  margin : 0;
  padding: 0;
  height: 100%;
  width: 100%;
  color: white;
  font-family: 'Exo 2'; }

$blue: #081c4f;
$black: #1a1a1a;

nav {
  display: flex;
  justify-content: flex-end;
  background: $black;
  height: 7%;
  position: fixed;
  width: 100%;
  top: 0px;
  ul {
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: flex-end;
    width: 60%;
    font-size: 20px;

    li {
      margin: auto; } } }

////////////////  ACCUEIL ///////////////
.accueil {
  background-image: url('../BG.png');
  background-repeat: no-repeat;
  background-position: center;
  height: 100%;
  width: 100%;

  .title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80%;
    h1 {
      font-size: 90px;
      font-weight: semibold; }
    p {
      font-size: 30px;
      text-align: center; } }

  .arrow {
    display: flex;
    justify-content: center; }
  object {
    height: 70px; }
  object:hover {
    color: black; } }

@media screen and (max-width: 700px) {
    nav {
      font-size: 14px;
      ul {
        width: 100%;
        padding: 0; } } }


////////////////// PROJETS ///////////////
h2 {
  display: flex;
  justify-content: center;
  margin: 0 0 2em 0;
  font-size: 50px; }

.projets {
  height : auto;
  background: radial-gradient($blue,$black);
  font-family: 'Exo 2';
  color: white;

  #projets-type {
    display: flex;
    justify-content: space-around;
    img {
      height: 200px;
      width: 218px; }
    figcaption {
      font-size: 35px;
      display: flex;
      justify-content: center; } } }
.project-card {
  display: flex;
  justify-content: space-around;
  padding : 10% 0; }


//////// Card 1 ///////
$duration : 0.5s;
.card {
    background-color: white;
    color: $black;
    display: block;
    width: 200px;
    box-shadow: 0 0 25px rgba(0,0,0,0.5);
    transition:  $duration box-shadow;
    text-align: center;
    border-radius: 10px;
    position: relative;
    img {
      border-radius: 10px 10px 0 0; } }

.hidden {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0,0,0, 0.9);
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: .5s ease;
  border-radius: 10px;
  color: white; }

.card:hover {
    box-shadow: 0 0 35px rgba(255,255,255,0.5);
    .hidden {
      height: 100%; } }

////////////////// Contact ///////////////

@import url(https://fonts.googleapis.com/css?family=Raleway:400,300,500,700);

$color1: #081C4F;
$color2: #1A1A1A;
$color3: white;


.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 20%;
    background-color: $color2;
    -webkit-font-smoothing: antialiased;
    font-size: 18px;
    h2 {
      margin: auto; } }
.buttons {
    display: flex;
    width: 100%;
    justify-content: space-around;
    margin: 30px; }

*, *:before, *:after {
    box-sizing: border-box; }


.icon {
	display: inline-block;
	width: 1em;
	height: 1em;
	fill: currentColor; }


.icon-paperplane {
    font-size: 1em;
    color: $color3;
    margin-left: 1px;
    margin-top: 5px; }


.contact-button {
    position: relative;
    -webkit-perspective: 1000;
    -webkit-backface-visibility: hidden;
    -webkit-transform:translate3d(0,0,0) {}
    color: $color3;
    text-decoration: none;
    text-transform: uppercase;
    font-family: 'Raleway', sans-serif;
    font-weight: 500;
    font-size: 1em;
    border: 2px solid $color3;
    border-radius: 4px;
    padding: 20px 60px 20px 60px;
    transition: 0.3s ease-in-out;

    span {
        text-transform: none;
        position: absolute;
        color: $color1;
        top: 20px;
        left: 35px;
        opacity: 0;
        transition: all 0s ease 0s;
        padding: auto; }

    &:hover {
        transition: 0.3s ease-in-out;
        border: 2px solid $color3;
        border-radius: 50px;
        background-color: $color3;

        &>span {
            opacity: 1;
            transition: all 0.25s ease-in-out 0.1s; }


        &:before {
            transition: 0.3s ease-in-out;
            background-color: $color1; } }


    &:before, &:after {
        content: "";
        position: absolute;
        top: -14px;
        left: -12px;
        width: 100px; // Changer la longuer de la barre
        height: 25px; }

    &:before {
        border: 2px solid $color1;
        background-color: $color2;
        transform: skew(-25deg);
        transition: 0.3s ease-in-out; }


    &:after {
        content: "Disponible";
        font-size: 0.5em;
        top: -7px;
        left: 0px;
        color: $color3; } }
